import React from 'react';
export default function Button({ children, disabled, style = {}, ...rest }) {
    const innerStyle = disabled ? {
        cursor: 'not-allowed',
        borderColor: '#d9d9d9',
        color: 'rgba(0,0,0,0.25)',
        backgroundColor: 'rgba(0,0,0,0.04)',
    } : {

    }
    return <div
        style={{
            padding: '6px 2px',
            height: '24px',
            borderRadius: '6px',
            backgroundColor: 'rgb(74, 144, 226)',
            color: 'white',
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            boxShadow: 'rgba(0, 0, 0, 0.1) 0px 1px 2px',
            transition: '0.3s',
            fontSize: '12px',
            userSelect: 'none',
            whiteSpace: 'nowrap',
            WebkitUserSelect: 'none',
            MozUserSelect: 'none',
            msUserSelect: 'none',
            ...innerStyle,
            ...style,
        }}
        {...rest}
    >
        {children}
    </div>
}
